export default {
  data () {
    return {
      orderNumber: ''
    }
  },
  onLoad (options) {
    this.orderNumber = options.orderNumber
    // 请求订单详情
    this.getOrderDetail()
  },
  methods: {
    getOrderDetail () {
      this.$request({
        url: '/api/public/v1/my/orders/chkOrder',
        data: {
          order_number: this.orderNumber
        },
        isAuth: true,
        method: 'POST'
      }).then(data => {
        console.log(data)
      })
    }
  }
}
